import React, { useState, useEffect, useContext } from "react";
import { Link, useHistory } from "react-router-dom";
import { userContext } from "../../App"
import "../../styles/Singin.css";
import M from "materialize-css";
import logo from "../../images/logoWhite.png"
import axios from "axios";
import { postearGetEntity } from "../AdminPanel/FetchFunctions";

const Login = () => {
  const history = useHistory();
  const [dni, setdni] = useState(null);
  const { /*state, */dispatch } = useContext(userContext);

  const PostData = () => {
    if (dni < 1000000) {
      M.toast({ html: "DNI Inválido", classes: "#c62828 red darken-3" });
    } else {
      axios.post("https://sietelotos.herokuapp.com/login",
        {
          dni: dni
        },
      )
        .then(success => {
          localStorage.setItem('nombre', success.data.nombre);
          localStorage.setItem('apellido', success.data.apellido);
          localStorage.setItem('tokenValido', success.headers.authorization);
          axios.defaults.headers['authorization'] = localStorage.getItem('tokenValido')
          localStorage.setItem("user", "usuario");
          dispatch({ type: "USER", payload: "user" });
          M.toast({
            html: `Bienvenida/o ${localStorage.nombre}`,
            classes: "#388e3c green darken-2",
          });
          console.log("success", success.headers.authorization);
          history.push("/");
        }
        )
        .catch(error => {
          console.log(error);
          M.toast({ html: "datos invalidos o el usuario no existe", classes: "#c62828 red darken-3" });
        });
    }
  };

  const [bkColor, setBkColor] = useState(null)

  const setBackGroundColor = (data) => {
    setBkColor(data[0].backgroundColorCardLogin)
  }

  useEffect(() => {
    if (!bkColor) {
      postearGetEntity({ entityClass: "settings", fx: setBackGroundColor })
    }
  }, []);

  return (
    <div className="mycard">
      {bkColor === null ? <div></div>
        :
        <div id="fondoTarjetaLogin" className="card auth-card input-field" style={{ backgroundColor: bkColor }}>
          <img alt="logo" className="logo-login" src={logo} />
          <div>
            <input
              type="number"
              id='inputLogin'
              placeholder="DNI"
              value={dni}
              onChange={(e) => setdni(e.target.value)}
            />
          </div>
          <button
            id="botonLogin"
            className="btn waves-effect waves-light #64b5f6 red darken-1"
            onClick={() => PostData()}
          >
            Ingresar
        </button>
          <h5 id="H5Register">
            <Link id="linkRegister" to="/login/admin">Ingresar como administrador</Link>
            <tr />
            <Link id="linkRegister" to="/register">Registrate acá</Link>
          </h5>
        </div>}
    </div>)
};

export default Login;
