import React, { useState, useEffect } from "react";
import "../../styles/Singin.css";
import { Link, useHistory } from "react-router-dom";
import M from "materialize-css";
import logo from "../../images/logoWhite.png"
import { postearGetEntity } from "../AdminPanel/FetchFunctions";

const Singup = () => {
  const history = useHistory();
  const [nombre, setnombre] = useState(null);
  const [apellido, setapellido] = useState(null);
  const [dni, setdni] = useState(null);

  const uploadFiedls = () => {
    if (!(nombre && apellido && dni)) {
      M.toast({ html: "Debe ingresar todos los datos", classes: "#c62828 red darken-3" });
    } else {
      fetch("https://sietelotos.herokuapp.com/register", {
        method: "POST",
        headers: {
          "Content-type": "application/json",
        },
        body: JSON.stringify({
          nombre,
          apellido,
          dni
        }),
      })
        .then((res) => {
          console.log(res)
          if (!res.ok) {
            M.toast({ html: "Datos invalidos o el usuario ya existe", classes: "#c62828 red darken-3" });
          } else {
            M.toast({
              html: "Ha creado su usuario",
              classes: "#388e3c green darken-2",
            });
            history.push("/login");
          }
        })
        .catch((err) => {
          console.log(err);
        });
    }
  };

  const PostData = () => {
    uploadFiedls();
  };

  const [bkColor, setBkColor] = useState(null)

  const setBackGroundColor = (data) => {
    setBkColor(data[0].backgroundColorCardLogin)
  }

  useEffect(() => {
    if (!bkColor) {
      postearGetEntity({ entityClass: "settings", fx: setBackGroundColor })
    }
  }, []);

  return (
    <div className="mycard">
      {bkColor === null ? <div></div>
        :
        <div id="fondoTarjetaLogin" style={{ backgroundColor: bkColor }} className="card auth-card input-field">
          <img alt='logo' className="logo-login" src={logo} />
          <input
            type="text"
            placeholder="Nombre"
            id='inputLogin'
            value={nombre}
            onChange={(e) => setnombre(e.target.value)}
          />
          <input
            type="text"
            placeholder="Apellido"
            id='inputLogin'
            value={apellido}
            onChange={(e) => setapellido(e.target.value)}
          />
          <input
            type="number"
            placeholder="DNI"
            id='inputLogin'
            value={dni}
            onChange={(e) => setdni(e.target.value)}
          />
          <button
            id="botonLogin"
            className="btn waves-effect waves-light #64b5f6 red darken-1"
            onClick={() => PostData()}
          >
            Registrar
        </button>
          <h5 id="H5Register">
            <Link id="linkRegister" to="/login">Ya estas registrado?</Link>
          </h5>
        </div>}
    </div>
  );
};

export default Singup;
